import React from 'react'
import ReactMarkdown from 'react-markdown'
import summary from '../content/output/summary.json'

let publications = []

const fileNames = Object.keys(summary.fileMap)
let keys = fileNames.filter((fileName) => {
  return fileName.includes('publications')
})



let hoge = require('../content/output/publications/iros-2020-hedayati.json')
let fuga
for (let key of keys) {
  fuga = key
  hoge = require(`../content/output/publications/${key.split('/'[3])}`)
  publications.push(`../${key}`)
}

class Publications extends React.Component {
  componentDidMount() {
  }

  render() {
    console.log(require(fuga))
    console.log(hoge)
    return (
      <div id="publications" >
         <h1>Publications</h1>
         <p>{ JSON.stringify(publications) }</p>
      </div>
    )
  }
}

export default Publications
